<template>
    <div>
        <button type="button" class="btn btn-xs btn-primary" @click="goEdit">
            <i class="glyphicon glyphicon-edit"></i> 
        </button>
        <button type="button" class="btn btn-xs btn-danger" @click="goToggleStatus" v-if="toStatusText()===''">
            <i class="glyphicon glyphicon-ban-circle"></i> 
        </button>
        <button type="button" class="btn btn-xs btn-success" @click="goToggleStatus($event)" v-if="toStatusText()===''">
            <i class="glyphicon glyphicon-ok-circle"></i> 
        </button>
    </div>
</template>
<script type="text/ecmascript-6">
    /**
     * 
     * operate-success 
     */
    import router from '../router.js';
    import store from '../store.js';
    import Interface from 'common/interface';
    import utility from 'ct-utility';
    import Const from 'common/const';
    export default{
        name: 'buttons',
        store,
        props: {
            prop: {
                // 
                type: String,
                default: ''
            },
            item: {
                // 
                type: Object,
                default(){
                    return {};
                }
            },
            index: {
                // 
                type: Number,
                default(){
                    return -1;
                }
            },
            data: {
                // 
                type: Array,
                default(){
                    return [];
                }
            }
        },
        methods: {
            /**
             * key
             */
            toStatusText(){
                return Const.getVal('switchStatus', this.item.disable);
            },
            /**
             * 
             */
            goEdit(){
                this.$store.dispatch('update', this.item.id);
                router.push({path: 'modal/update', append: true});
            },
            /**
             * /
             */
            goToggleStatus(e){
                e.stopPropagation();
                var that = this;
                var text = this.toStatusText() === '' ? '' : '';

                popoverConfirm.init({
                    UID: this.item.id,
                    title: '' + text + this.item.sourceAppName + '',
                    loadingContent: text + this.item.sourceAppName + '...',
                    $trigger: $(e.currentTarget),
                    ajax: {
                        config: {
                            type: 'get',
                            url: Interface.appSwitch.updateStatus,
                            data: {
                                id: this.item.id,
                                disable: !this.item.disable
                            }
                        },
                        callback: function(res) {
                            res = utility.objTransfer.lowerKey(res);
                            if (res.status) {
                                popoverConfirm.setContent({
                                    title: '',
                                    content: '<span class="text-success">' + text + '</span>'
                                });
                                setTimeout(()=>{
                                    popoverConfirm.destroy();
                                    that.$parent.$emit('operate-success');
                                }, 2000);
                            } else {
                                popoverConfirm.setContent({
                                    title: '',
                                    content: '<span class="text-danger">' + res.message + '</span>'
                                });
                            }
                        },
                        error: function(xhr) {
                            var msg = '';

                            if (xhr.status === 401) {
                                msg = '';
                            } else {
                                msg = '';
                            }
                            popoverConfirm.setContent({
                                title: '',
                                content: '<span class="text-danger">' + msg + '</span>'
                            });
                        }
                    }
                });
            }
        }
    };
</script>



